.header {
  margin-top: 1.4rem;
  .navigation {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 3rem;
    background-color: $white;
    height: 4rem;
    .logo-and-searchbar {
      flex: 1;
      display: flex;
      align-items: center;
      .logo {
        display: flex;
        align-items: center;
        text-decoration: none;
        .logo-slogan {
          display: flex;
          flex-direction: column;
          white-space: nowrap;
          margin-left: 0.8rem;
          flex-direction: column;
          .logo_laplace {
            width: fit-content;
          }
          .slogan {
            margin: 0;
            font-family: $secondary_font;
            font-family: Montserrat;
            font-size: 1.2rem;
            font-weight: $light;
            color: $grayDark;
          }
        }
      }
      .search-bar {
        margin-left: 7.7rem;
        width: 100%;
        form {
          display: inline-flex;
          background-color: $grayLighter;
          width: 100%;
          padding: 1.1rem 1.7rem;
          border-radius: 20px;
          background-color: $grayLighter;
          .button-search {
            display: flex;
            align-items: center;
            border: none;
            padding: 0;
            cursor: pointer;
            img {
              width: 1.3rem;
              height: 1.3rem;
            }
          }
          .search-div {
            flex-grow: 1;
            display: flex;
            .search-input {
              flex-grow: 1;
              width: 100%;
              background: none;
              border: none;
              padding-left: 10px;
              padding-bottom: 2px;
              &::placeholder {
                color: $gray;
              }
              &:focus {
                outline: none;
                color: $blackLight;
              }
              &::-webkit-search-cancel-button {
                -webkit-appearance: none;
              }
            }
          }
        }
      }
    }
    .navigation-about {
      padding-left: 6.2rem;
      padding-right: 3rem;
      ul {
        display: inline-block;
        padding-left: 0;
        margin: 0;
        li {
          display: inline-block;
          font-size: 1.4rem;
          font-weight: $semiBold;
          a {
            display: flex;
            flex-direction: column;
            align-items: center;
            color: $gray;
          }
        }
      }
    }
    .navigation-join {
      display: flex;
      .btn-submit {
        display: inline-block;
        margin-right: 3rem;
        button {
          color: $gray;
          font-weight: $semiBold;
          font-size: 1.4rem;
          padding: 0.6rem 1.1rem;
          background-color: transparent;
          border: 1px solid $grayLight;
        }
      }
      .btns-join {
        display: inline-flex;
        align-items: center;
        position: relative;
        a:first-of-type {
          border-color: transparent;
          border-radius: 0;
          box-shadow: none;
          margin-right: 14px;
          font-size: 1.4rem;
          font-weight: $semiBold;
          color: $gray;
          margin-right: 3rem;
          &::before {
            position: absolute;
            top: 0;
            left: -15px;
            width: 1px;
            height: 32px;
            content: "";
            background-color: #d1d1d1;
            display: inline-block;
          }
        }
        a:nth-child(2) {
          background-color: $green;
          color: $white;
          padding: 0.6rem 1rem 0.6rem 1.1rem;
        }
      }
    }
  }
}
.navigation {
  display: inline-flex;
  align-items: center;
  margin-bottom: 0;
  margin-top: 2rem;
  div {
    display: flex;
    ul {
      display: flex;
      padding-left: 0;
      margin-top: 0;
      a {
        text-align: center;
        font-weight: $semiBold;
        font-size: 1.4rem;
        margin-left: 3rem;
        color: $gray;
      }
    }
  }
  .div-separator {
    padding-left: 3rem;
    .separator {
      background-color: #d1d1d1;
      width: 0.1rem;
      height: 3rem;
      display: block;
    }
  }
  .container-btn-navigation {
    display: flex;
    align-items: center;
    width: 80%;
    overflow-x: scroll;
    position: relative;
    -ms-overflow-style: none;
    scrollbar-width: none; /* Firefox */
    &::-webkit-scrollbar {
      display: none;
    }
    .div-navigation {
      max-width: 100%;
      ul {
        max-width: 100%;
        li {
          display: list-item;
          white-space: nowrap;
        }
      }
    }
  }
  .btn-arrow {
    position: absolute;
    height: fit-content;
    background-color: $white;
    &.btn-left {
      cursor: pointer;
      display: flex;
      justify-content: flex-start;
      border: none;
      left: 123px;
      padding: 0;
      padding-right: 2rem;
      width: 10rem;
      z-index: 1;
      /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&1+0,0+100;White+to+Transparent */
      background: -moz-linear-gradient(
        left,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
      ); /* FF3.6-15 */
      background: -webkit-linear-gradient(
        left,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
      ); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(
        to right,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
      ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=1 ); /* IE6-9 */
      display: none;
      &.active {
        display: flex;
      }
    }
    &.btn-right {
      cursor: pointer;
      display: flex;
      justify-content: flex-end;
      padding-right: 0;
      left: 168rem;
      width: 10rem;
      border: none;
      /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&1+0,0+100;White+to+Transparent */
      background: -moz-linear-gradient(
        right,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
      ); /* FF3.6-15 */
      background: -webkit-linear-gradient(
        right,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
      ); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(
        to left,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
      ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=1 ); /* IE6-9 */
      display: none;
      &.active {
        display: flex;
      }
    }
  }
  .view-all {
    padding-left: 3rem;
  }
  .menu-item {
    a {
      &.active {
        border-bottom: 0.6rem solid #000;
        padding-bottom: 1.8rem;
        position: relative;
        z-index: 2;
      }
    }
  }
}
